import React from 'react'
import {Switch, Route, Link} from 'react-router-dom'


const Menu_display1 = (props) => {
    const {match} = props;
    return <div>
        <Link style={{margin:"0 12px"}} to={`${match.path}/1`}>产品1</Link>
        <span style={{margin:"0 12px"}}>|</span>
        <Link style={{margin:"0 12px"}} to={`${match.path}/2`}>产品2</Link>
        <hr/>
        <Switch>
            <Route path={`${match.path}/:id`} component={(props) => {
                const {match: {params}} = props;
                return <div>产品 {params.id} page!!</div>
            }}/>
        </Switch>
    </div>
}
export default Menu_display1